import "./modal.scss";
import { useState, useEffect } from "react";
import { FormGroup } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Modal from "react-bootstrap/Modal";
import validator from 'validator';

const Modalmy = (props) => {
  const [show, setShow] = useState(true);
  const [formData, setFormData] = useState({
    name: '',
    phone: '',
    site: 'Corporate',
  });


  const handleClose = () => {
    setShow(!show);
  }
  const [errors, setErrors] = useState({});


  //      
  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setFormData( {...formData, [name]: value });
  };

  //     
  const validateFormData = () => {
    let validationErrors = {};
    if (validator.isEmpty(formData.name)) {
      validationErrors.name = '   ';
    }
    if (!phoneRegex.test(formData.phone)) {
      validationErrors.phone = 'Phone number is invalid';
    }
    setErrors(validationErrors);
    return Object.keys(validationErrors).length === 0;
  };

    //     
    const handleSubmit = (event) => {
      event.preventDefault();
      if (validateFormData()) {
        console.log(formData);
        handleClose();
        alert(',   ')
      }
     
    };

    const phoneRegex = /^0\d{2}\d{3}\d{4}$/;

  return (
   <>
      <Modal show={show} onHide={handleClose}>
        {" "}
        <Modal.Header closeButton>
          {" "}
          <Modal.Title> </Modal.Title>{" "}
        </Modal.Header>{" "}
        <Modal.Body>
          {" "}
          <Form className="formGoogle" onSubmit={handleSubmit}>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <div className="result-name result"></div>
              <div className="result-phone result"></div>
              <Form.Label> '</Form.Label>{" "}
              <Form.Control
                type="text"
                name="name"
                value={formData.name}
                placeholder="'"
                onChange={handleInputChange}
                className="form-name"
                autoFocus
              />
            </Form.Group>{" "}
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>  </Form.Label>
              <Form.Control
                type="tel"
                name = "phone"
                value={formData.phone}
                placeholder=" - 097 000 00 00"
                className="form-phone"
                onChange={handleInputChange}
              />
            </Form.Group>{" "}
            <FormGroup>
              <label>
                  ,   :
                <select onChange={handleInputChange}>
                  <option value="Corporate"></option>
                  <option value="CardSite"> </option>
                  <option value="Landing">Landing</option>
                </select>
              </label>
            </FormGroup>
            <div style={{color:'red'}}>{errors.phone}</div>
            <div style={{color:'red'}}>{errors.name}</div>
          
            <br></br>
            <Button variant="primary"  type="submit">
            {" "}
            {" "}
          </Button>{" "}
          </Form>{" "}
        </Modal.Body>{" "}
        <Modal.Footer>
          {" "}
          <Button variant="secondary" onClick={handleClose}>
            {" "}
            Close{" "}
          </Button>{" "}

         
        </Modal.Footer>{" "}
      </Modal>{" "}
    </>
  );
};;

export default Modalmy;
